import React from 'react';
import {
  LineChart,
  XAxis,
  YAxis,
  CartesianGrid,
  Legend,
  Line,
  Tooltip,
  ResponsiveContainer,
} from 'recharts';

interface ReportProps {
  report: { date: string; cases: number; deaths: number }[];
  scale: 'log' | 'linear';
}

const deathsColor = '#F44336';
const casesColor = 'rgb(0, 136, 254)';
export default function ReportLineChart({ report, scale }: ReportProps) {
  return (
    <div className="mb-4">
      <h3 className="bg-gray-100 p-2 mb-2 font-bold">
        Evolution of cases and deaths since {report[0].date}, {scale} scale
      </h3>
      <div style={{ width: '100%', height: 200, maxWidth: 500 }}>
        <ResponsiveContainer>
          <LineChart data={report} barSize="big">
            <XAxis dataKey={'date'} allowDataOverflow={true} />
            <YAxis dataKey="cases" scale={scale} domain={[1, 'auto']} />
            <CartesianGrid strokeDasharray="1 1" />
            <Tooltip />
            <Legend />
            <Line
              type="monotone"
              dataKey="cases"
              stroke={casesColor}
              dot={{ r: 2 }}
            />{' '}
            <Line
              type="linear"
              dataKey="deaths"
              stroke={deathsColor}
              dot={{ r: 2 }}
            />
          </LineChart>
        </ResponsiveContainer>
      </div>
    </div>
  );
}
