import React from 'react';

import {
  XAxis,
  YAxis,
  Bar,
  BarChart,
  Tooltip,
  ResponsiveContainer,
  Legend,
} from 'recharts';
interface ReportBarchartProps {
  report: { date: string; cases: number; deaths: number }[];
}
const deathsColor = '#F44336';
const casesColor = 'rgb(0, 136, 254)';
export default function ReportBarChart({ report }: ReportBarchartProps) {
  const data = report.slice(-14);
  return (
    <div className="mb-4">
      <h3 className="bg-gray-100 p-2 mb-2 font-bold">
        Cases and deaths per day in the last 2 weeks
      </h3>
      <div style={{ width: '100%', height: 200, maxWidth: 500 }}>
        <ResponsiveContainer>
          <BarChart data={data} height={200} width={500}>
            <Tooltip content={<CustomTooltip />} />
            <Legend />
            <XAxis dataKey="date" />
            <YAxis dataKey="todayCases" />
            <Bar name="cases" dataKey="todayCases" fill={casesColor} />
            <Bar name="deaths" dataKey="todayDeaths" fill={deathsColor} />
          </BarChart>
        </ResponsiveContainer>
      </div>
    </div>
  );
}
const CustomTooltip = ({ active, payload, label }: any) => {
  if (active) {
    return (
      <div className="bg-white border-gray-100 p-2 border border-solid">
        <p>{label}</p>
        <p className="label">{`cases: ${payload[0].value}`}</p>
        <p className="label">{`deaths: ${payload[1].value}`}</p>
      </div>
    );
  }

  return null;
};
